            <div
              class="flex flex-col h-[244px] justify-between px-[20px] py-[20px] tablet:px-[20px] tablet:py-[18px] desktop:px-[28px] desktop:py-[26px]"
            >
              <div>
                <EmphasisSpan
                  class="mr-[10px]"
                  v-for="(type, typeIndex) in slide.type"
                  :key="typeIndex"
                >
                  {{ type }}
                </EmphasisSpan>
                <p
                  class="text-[22px] mt-[22px] mb-[4px] font-800 text-black-b800"
                >
                  {{ slide.name }}
                </p>
                <p class="text-[14px] font-700 text-black-b700">
                  {{ slide.date }}
                </p>
              </div>
              <button
                click="openModal(slide)"
                class="flex justify-center absolute bottom-[18px] tablet:bottom-[20px] desktop:bottom-[28px] bg-black-b00 border border-black-b40 w-[232px] tablet:w-[336px] h-[50px] leading-[50px] text-[14px] text-center font-800 rounded-full"
                @click="openModal(slide)"
              >
                <span class="font-800 text-black-b600"> </span>
                <img
                  class="inline ml-[10px] w-[22px]"
                  src="@/assets/image/icons/arrow_forward_black.svg"
                  alt=""
                />
              </button>
            </div>
          </template>
        </div>
      </div>
    </div>
    <!--  -->
    <modalPage
      v-if="activeSlide"
      :content="activeSlide"
      :isVisible="isModalVisible"
      @close="closeModal"
    />
  </div>
</template>
<script>
//--------------- -----------------

import slide1 from "@/assets/image/portfolio/solas.png";
import companyLogo1 from "@/assets/image/logo.svg";
import modalImage1 from "@/assets/image/portfolio/.jpg";

import slide2 from "@/assets/image/portfolio/solas.png";
import companyLogo2 from "@/assets/image/logo.svg";
import modalImage2 from "@/assets/image/portfolio/.jpg";

import slide3 from "@/assets/image/portfolio/solas.png";
import companyLogo3 from "@/assets/image/logo.svg";
import modalImage3 from "@/assets/image/portfolio/.jpg";

import slide4 from "@/assets/image/portfolio/solas.png";
import companyLogo4 from "@/assets/image/logo.svg";
import modalImage4 from "@/assets/image/portfolio/.jpg";

import slide5 from "@/assets/image/portfolio/solas.png";
import companyLogo5 from "@/assets/image/logo.svg";
import modalImage5 from "@/assets/image/portfolio/.jpg";

import slide6 from "@/assets/image/portfolio/solas.png";
import companyLogo6 from "@/assets/image/logo.svg";
import modalImage6 from "@/assets/image/portfolio/.jpg";

import slide7 from "@/assets/image/portfolio/solas.png";
import companyLogo7 from "@/assets/image/logo.svg";
import modalImage7 from "@/assets/image/portfolio/.jpg";

//--------------------------------
import moreHoriz from "@/assets/image/icons/more_horiz.svg";
import teamGGZ from "@/assets/image/icons/teamGGZ.svg";
//--------------- -----------------
import EmphasisSpan from "@/components/partials/EmphasisSpan.vue";
import SlideButton from "@/components/partials/slideButton.vue";
//--------------------------------
import modalPage from "@/components/partials/modalPage.vue";
export default {
  components: {
    SlideButton,
    modalPage,
    EmphasisSpan,
  },
  data() {
    return {
      currentIndex: 0,
      slideDimensions: { width: 380, height: 490 },
      visibleSlides: 3, //     
      slideWidth: 380, //    (px)
      gap: 16, //    (px)
      activeSlide: null, //   
      isModalVisible: false, //  / 
      filteredSlides: [], //  
      isDropdownOpen: false, //   
      selectedOption: "", // : 
      options: ["", "", ""], //  
      baseSlides: [
        {
          image: slide1,
          companyLogo: companyLogo1,
          modalImage: modalImage1,
          alt: "Slide 1",
          name: "Web  1",
          date: "2023.01.01~10.31",
          type: ["WEB"],
          platform: [""],
          contribution: "80%",
          liveUrl: null,
          description: "     .",
          companyType: "",
        },
        {
          image: slide2,
          companyLogo: companyLogo2,
          modalImage: modalImage2,
          alt: "Slide 2",
          name: "App  2",
          date: "2023.01.01~10.31",
          type: ["APP", "WEB"],
          platform: [" ", ""],
          contribution: "100%",
          liveUrl: "https://example.com/project2",
          description: "     .",
          companyType: "",
        },
        {
          image: slide3,
          companyLogo: companyLogo3,
          modalImage: modalImage3,
          alt: "Slide 3",
          name: "Web  3",
          date: "2023.01.01~10.31",
          type: ["WEB"],
          platform: [""],
          contribution: "70%",
          liveUrl: "https://example.com/project3",
          description:
            "      .",
          companyType: "",
        },
        {
          image: slide4,
          companyLogo: companyLogo4,
          modalImage: modalImage4,
          alt: "Slide 4",
          name: "App  4",
          date: "2023.01.01~10.31",
          type: ["APP"],
          platform: [""],
          contribution: "90%",
          liveUrl: "https://example.com/project4",
          description:
            "      .",
          companyType: "",
        },
        {
          image: slide5,
          companyLogo: companyLogo5,
          modalImage: modalImage5,
          alt: "Slide 5",
          name: "Web  5",
          date: "2023.01.01~10.31",
          type: ["WEB"],
          platform: [""],
          contribution: "85%",
          liveUrl: "https://example.com/project5",
          description:
            "       .",
          companyType: "",
        },
        {
          image: slide6,
          companyLogo: companyLogo6,
          modalImage: modalImage6,
          alt: "Slide 6",
          name: "App  6",
          date: "2023.01.01~10.31",
          type: ["APP"],
          platform: [""],
          contribution: "95%",
          liveUrl: "https://example.com/project6",
          description:
            "   UX/UI    .",
          companyType: "",
        },
        {
          image: slide7,
          companyLogo: companyLogo7,
          modalImage: modalImage7,
          alt: "Slide 7",
          name: "Web  7",
          date: "2023.01.01~10.31",
          type: ["WEB"],
          platform: [""],
          contribution: "75%",
          liveUrl: "https://example.com/project7",
          description: "      .",
          companyType: "",
        },
      ],
    };
  },
  created() {
    this.filterSlides(""); // :  
  },
  computed: {
    slides() {
      const requiredSlides = 3; //   
      const currentSlides = this.baseSlides.length;
      const missingSlides = Math.max(0, requiredSlides - currentSlides); //    

      // " "  
      if (missingSlides > 0) {
        const placeholderSlides = Array.from({ length: missingSlides }, () => ({
          image: teamGGZ, //  
          alt: " ",
          name: ".",
          date: "0000.00.00 ~ 00.00",
          companyLogo: moreHoriz, //   
          type: [" "],
          companyType: " ", // " "  
        }));

        return [...this.baseSlides, ...placeholderSlides];
      }

      return this.baseSlides; //    
    },
  },
  mounted() {
    window.addEventListener("resize", this.updateSlideDimensions);
    this.updateSlideDimensions(); // 
    this.checkIsMobile();
    window.addEventListener("resize", this.checkIsMobile);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.updateSlideDimensions);
    window.removeEventListener("resize", this.checkIsMobile);
  },
  methods: {
    //w-[270px] h-[440px] mobile:w-[320px] mobile:h-[466px] desktop:w-[320px] desktop:h-[490px]

    updateSlideDimensions() {
      this.slideDimensions = this.getSlideDimensions();
    },
    getSlideDimensions() {
      const width = window.innerWidth;

      if (width <= 959) {
        return { width: 272, height: 440 }; //   
      } else if (width >= 960 && width <= 1279) {
        return { width: 320, height: 466 }; //   
      } else if (width >= 1280) {
        return { width: 380, height: 490 }; //   
      }
      return { width: 440, height: 490 }; // 
    },
    nextSlide() {
      if (this.currentIndex < this.filteredSlides.length - this.visibleSlides) {
        this.currentIndex++;
      }
    },
    prevSlide() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    openModal(slide) {
      this.activeSlide = slide;
      this.isModalVisible = true;
      document.body.classList.add("no-scroll"); //  
    },
    closeModal() {
      this.activeSlide = null;
      this.isModalVisible = false;
      document.body.classList.remove("no-scroll"); //  
    },
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen; //    
    },
    selectOption(option) {
      this.selectedOption = option; //   
      this.isDropdownOpen = false; //  
      this.filterSlides(option); //   
    },
    filterSlides(type) {
      this.isDropdownOpen = false; //  

      if (type === "") {
        // ""    
        const requiredSlides = 3; //   
        const currentSlides = this.baseSlides.length;
        const missingSlides = Math.max(0, requiredSlides - currentSlides); //    

        // " "  
        const placeholderSlides = Array.from({ length: missingSlides }, () => ({
          image: teamGGZ, //  
          alt: " ",
          name: ".",
          date: "0000.00.00 ~ 00.00",
          companyLogo: moreHoriz, //   
          type: [" "], //  
          companyType: " ", // " "  
        }));

        this.filteredSlides = [...this.baseSlides, ...placeholderSlides]; //   + " " 
      } else {
        // ""  "" 
        const filteredBaseSlides = this.baseSlides.filter(
          (slide) => slide.companyType === type
        );

        const requiredSlides = 3; //   
        const currentSlides = filteredBaseSlides.length;
        const missingSlides = Math.max(0, requiredSlides - currentSlides); //    

        // " "  
        const placeholderSlides = Array.from({ length: missingSlides }, () => ({
          image: teamGGZ, //  
          alt: " ",
          name: ".",
          date: "0000.00.00 ~ 00.00",
          companyLogo: moreHoriz, //   
          type: [" "], //  
          companyType: " ", // " "  
        }));

        this.filteredSlides = [...filteredBaseSlides, ...placeholderSlides]; //   + " " 
      }

      //     
      this.currentIndex = 0;
    },
    checkIsMobile() {
      this.isMobile = window.innerWidth <= 960;
    },
  },
};
</script>
